@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  position: fixed;
  background: #1C3E6F;
}

.nav-item a {
  color: white;
  font-weight: bold;
  font-size: 20px;
  margin-right: 20px;
}

.nav-item a:hover {
  color: #C4D1E4;
}

.navbar-brand img {
  margin-left: 20px;
  max-width: 40px;
}


*{
  font-family: 'Fredoka One', cursive;
  letter-spacing: 3px !important;
  color: #1C3E6E;
}

p {
  color: #8198B8;
}

.page-content {
  background-color: #F2F4F8;
}

.course-template .course-title {
  padding-top: 5%;;
  margin-bottom: 3%;
}

.col-item {
  background-color: white;
  border-radius: 15px;
  margin-right: 10px;
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 2%;
  padding-bottom: 2%;
}

.course-template .syllabus {
  vertical-align: center;
  padding-top: 3%;
  padding-bottom: 3%;
}

.course-template .col-item h3 {
  margin-top: 30px;
  margin-left: 4%;
}

.course-template .col-item h4 {
  margin-top: 5%;
  margin-left: 4%;
}

.course-template .col-item p {
  margin-top: 20px;
  width: 90%;;
  margin-left: 4%;
  margin-bottom: 3%;
  color: #5C779C;
}

.course-template .course-description, .course-requisite {
  margin-left: 0;
}

.requisites-display {
  height: 120px;
  margin-top: 2%;
  background-color: #EFF3FB;
  margin-left: 5%;
  border-radius: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-bottom: 4%;
}

.expand_button {
  margin-top: -5%;
}

.syllabus-link {
  margin-top: 2%;
    border-radius: 15px;
    background-color: #1C3E6E;
    color: white;
    width: 8em;
    height: 3em;
    border: none;
    font-size: 1.2em;
    text-align: center;
}


*{
    font-family: 'Fredoka One', cursive;
    letter-spacing: 3px !important;
    color: #1C3E6E;
}

.wishlist-page-content {
    background-color: white;
    margin-bottom: auto;

}

.left-panel {
    width: 75%; 
    position: fixed; 
    z-index: 1; 
    overflow-x: hidden; 
    padding-top: 20px; 
    left: 0; 
    background-color: white;
    margin-left: 5%;
    margin-top: 2%;
}
  
.right-panel {
    height: 100vh;
    width: 25%;
    position: fixed;
    z-index: 1;
    overflow-x: hidden;
    padding-top: 20px;
    right: 0;
    background-color: #F1F6FD;
}

.right-panel h3 {
    margin-top: 3%;
    margin-bottom: 3%;
}
  
.centered {
    position: absolute;
    width: 70%;
    margin-top: 300px;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.centered img {
    margin-top: 3%;
}

.add-button {
    background-color: white;
    color: #C4C4C4;
    border: 1px solid #E4E4E4;
    box-shadow: 0px 5px 4px rgba(0,0,0,0.2);
    box-sizing: border-box;
    border-radius: 10px;
    margin: 10% 2%;
}

.add-button:hover {
    background-color: #1C3E6E;
    color: white;
}

.add-button:active {
    transform: translateY(4px);
    background-color: white;
    color: #C4C4C4;
    border: 1px solid #E4E4E4;
    box-shadow: 0px 5px 4px rgba(0,0,0,0.2);
    box-sizing: border-box;
    border-radius: 10px;
    margin: 10% 2%;
}

.add-button:visited, .add-button:focus {
    background-color: white;
    color: #C4C4C4;
    border: 1px solid #E4E4E4;
    box-shadow: 0px 5px 4px rgba(0,0,0,0.2);
    box-sizing: border-box;
    border-radius: 10px;
    margin: 10% 2%;
}

Dropdown.Item:hover {
    background-color: #1C3E6E;
    color: white;
}

.course-card-container {
    overflow-x: scroll;
    width: 80%;
}
.coursecard{
  color: red;
}
.tags {
    color: black;
    align-items: left;
    font-size: 70%;
  }
  .card-subtext {
    text-align: left;
    font-size: 70%;
  }

.wishlist-card {
  width: 85%;
  border-radius: 2em;
  border: 3px solid #C4C4C4;
  text-align: center;
  padding: 3%;
  margin-top: 3%;
  text-decoration: none;
}

.wishlist-link:link {
  text-decoration: none;
}

.course-card-render {
  display: flex;
  flex-direction: column;
}
  
.sign-up {
    margin-top: 5%;
}

.signup-input {
    margin-top: 2%;
    width: 30%;
    height: 3em;
    padding-left: 1%;
    border-radius: 15px;
    color: #C4C4C4;
    border: 2px solid #C4C4C4
}

.signup-button {
    margin-top: 2%;
    border-radius: 15px;
    background-color: #1C3E6E;
    color: white;
    width: 8em;
    height: 3em;
    border: none;
    font-size: 1.2em;
    text-align: center;
}
.result-display {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 3px solid #C4C4C4;
    border-radius: 20px;
    background-color: white;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    padding-top: 2%;
    padding-bottom: 2%;
    margin-top: 3%;
    margin-bottom: 3%;
}

.search-result-display {
    margin-top: 3%;
    margin-bottom: 3%;
}

.search {
    margin-top: 3%;
}

.submit-button {
    background-color: #1C3E6E;
    color: white;
    border: none;
    border-radius: 15px;
    border: none;
    height: 3em;
    width: 7em;
}

.text-input {
    width: 40%;
    height: 3em;
    border-radius: 15px;
    border: 2px solid #C4C4C4;
    margin-right: 1%;
    padding-left: 2%;
}

.label {
    background-color: #F1F6FD;
    color: #1C3E6E;
    border-radius: 15px;
    margin-bottom: 3%;
    height: 4em;
    justify-content: center;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: row;
}
